
@import "../../scss/_global.scss";

.button {
  @extend %button;
  margin: 24px auto;

  .button-text {
    @media (max-width: 375px) {
      font-size: 12px !important;
    }
  }
}

.user-info {
  display: flex;
  gap: 14px;
  flex-direction: column;

  @media (max-width: 576px) {
    gap: 10px;
    max-width: 330px;
    margin: 0 auto;
  }
}

.decr {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #2b2b2b;

  @media (max-width: 576px) {
    font-size: 12px;
    line-height: 14px;
  }
}
