@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
#questions .questions-wrapper {
  background: #ffffff;
  border: 3px solid #ececec;
  box-shadow: 15px 15px 40px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
}

#questions {
  padding-bottom: 100px;
}
@media (max-width: 992px) {
  #questions {
    padding-bottom: 80px;
  }
}
@media (max-width: 768px) {
  #questions {
    padding-bottom: 60px;
  }
}

#questions .questions-wrapper {
  display: flex;
  align-items: center;
}
@media (max-width: 992px) {
  #questions .questions-wrapper {
    border: none;
    box-shadow: none;
    border-radius: 0;
    justify-content: center;
  }
}
#questions .questions-wrapper__content {
  padding: 70px;
}
#questions .questions-wrapper__content form {
  max-width: 585px;
}
@media (min-width: 993px) {
  #questions .questions-wrapper__content {
    max-width: 50%;
  }
}
@media (max-width: 1440px) {
  #questions .questions-wrapper__content {
    padding: 55px;
  }
}
@media (max-width: 1366px) {
  #questions .questions-wrapper__content {
    padding: 40px;
  }
}
@media (max-width: 1280px) {
  #questions .questions-wrapper__content {
    padding: 30px;
  }
}
@media (max-width: 1024px) {
  #questions .questions-wrapper__content {
    padding: 24px;
  }
}
@media (max-width: 992px) {
  #questions .questions-wrapper__content {
    padding: 0;
    text-align: center;
    max-width: 520px;
    width: 100%;
  }
}
#questions .questions-wrapper__content h2 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 100%;
  color: #2b2b2b;
  margin-bottom: 30px;
  margin-top: 40px;
  font-size: calc(0.021574973 * 100vw + 18.5760517799px);
}
@media (max-width: 1280px) {
  #questions .questions-wrapper__content h2 {
    margin-top: 20px;
  }
}
@media (max-width: 1150px) {
  #questions .questions-wrapper__content h2 {
    margin-top: 0px;
    margin-bottom: 20px;
  }
}
@media (min-width: 1921px) {
  #questions .questions-wrapper__content h2 {
    font-size: 60px;
  }
}
@media (max-width: 993px) {
  #questions .questions-wrapper__content h2 {
    font-size: 40px;
  }
}
@media (max-width: 576px) {
  #questions .questions-wrapper__content h2 {
    margin-bottom: 14px;
    font-size: 28px;
    line-height: 100%;
  }
}
#questions .questions-wrapper__content .subtitle {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 115%;
  color: #2b2b2b;
  margin-bottom: 40px;
  font-size: calc(0.0075512406 * 100vw + 7.501618123px);
}
@media (max-width: 1150px) {
  #questions .questions-wrapper__content .subtitle {
    margin-bottom: 20px;
  }
}
@media (max-width: 992px) {
  #questions .questions-wrapper__content .subtitle {
    max-width: 400px;
    margin: 0 auto 20px;
    width: 100%;
  }
}
@media (min-width: 1921px) {
  #questions .questions-wrapper__content .subtitle {
    font-size: 22px;
  }
}
@media (max-width: 993px) {
  #questions .questions-wrapper__content .subtitle {
    font-size: 15px;
  }
}
@media (max-width: 576px) {
  #questions .questions-wrapper__content .subtitle {
    margin-bottom: 14px;
    font-size: 12px;
    line-height: 14px;
  }
}
#questions .questions-wrapper__content .phone {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 115%;
  color: #2b2b2b;
  margin-bottom: 40px;
  font-size: calc(0.0172599784 * 100vw + 6.8608414239px);
}
@media (max-width: 1150px) {
  #questions .questions-wrapper__content .phone {
    margin-bottom: 25px;
  }
}
@media (min-width: 1921px) {
  #questions .questions-wrapper__content .phone {
    font-size: 40px;
  }
}
@media (max-width: 993px) {
  #questions .questions-wrapper__content .phone {
    font-size: 24px;
  }
}
@media (max-width: 576px) {
  #questions .questions-wrapper__content .phone {
    margin-bottom: 14px;
    font-size: 22px;
  }
}
#questions .questions-wrapper__img {
  display: none;
}
@media (min-width: 993px) {
  #questions .questions-wrapper__img {
    display: block;
    position: relative;
    align-self: stretch;
    flex-grow: 1;
    margin-right: 2%;
    overflow: hidden;
  }
}
#questions .questions-wrapper__img img {
  position: absolute;
  bottom: -7px;
  right: 0;
  display: block;
  width: 100%;
}