.button {
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  display: block;
  position: relative;
  width: 300px;
  height: 78px;
  transition: transform 0.3s ease-in;
  border-radius: 40px;
}
@media (max-width: 992px) {
  .button {
    width: 230px;
    height: 65px;
  }
}
@media (max-width: 576px) {
  .button {
    width: 150px;
    height: 40px;
  }
}
.button .button-background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: block;
  background-color: #468217;
  transform-origin: center;
  transition: all 0.2s ease;
  box-shadow: 0px 6px 24px rgba(70, 130, 23, 0);
  z-index: 2;
  border-radius: 40px;
  overflow: hidden;
}
.button .button-background.button-animation::before {
  content: "";
  opacity: 0.7;
  position: absolute;
  top: 0;
  transform: translateX(-125%);
  width: 100%;
  height: 100%;
  background: linear-gradient(100deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 25%, hsla(0, 0%, 84%, 0.25) 51%, rgba(0, 0, 0, 0) 76%, rgba(0, 0, 0, 0));
  filter: blur(2px);
  transition: opacity 1s ease;
  animation: sheen 3s forwards infinite;
}
.button .button-text {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 100%;
  color: #ffffff;
  z-index: 3;
  position: relative;
}
@media (max-width: 992px) {
  .button .button-text {
    font-size: 18px;
  }
}
@media (max-width: 576px) {
  .button .button-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
  }
}
.button:not(:disabled):hover .button-background, .button:not(:disabled):focus .button-background {
  transition: all 0.2s ease-in 0.1s;
  background-color: #417915;
  box-shadow: 0px 6px 24px rgba(70, 130, 23, 0.39);
}
.button:not(:disabled):hover .button-background::before, .button:not(:disabled):focus .button-background::before {
  opacity: 0;
}
.button:not(:disabled):hover .button-background {
  transform: scale(0.96);
}
.button:not(:disabled):active {
  transform: translateY(3px);
}
@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
.button:disabled .button-background {
  background-color: rgba(95, 95, 95, 0.8784313725);
}

.button {
  margin: 24px auto;
}
@media (max-width: 375px) {
  .button .button-text {
    font-size: 12px !important;
  }
}

.user-info {
  display: flex;
  gap: 14px;
  flex-direction: column;
}
@media (max-width: 576px) {
  .user-info {
    gap: 10px;
    max-width: 330px;
    margin: 0 auto;
  }
}

.decr {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #2b2b2b;
}
@media (max-width: 576px) {
  .decr {
    font-size: 12px;
    line-height: 14px;
  }
}